import { TimeEntryDetailsComponent } from './time-entry-details/time-entry-details.component';
import { TimeEntryCreateComponent } from './time-entry-create/time-entry-create.component';
import { TaskEntryLandingPageComponent } from './task-entry-landing-page/task-entry-landing-page.component';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [{
  path: '',
  component: TaskEntryLandingPageComponent,
  data: {
    title: 'Time Entry',
    mainMenu: 'Time Entry',
    hideTitle: true,
    urls: []
  }
},
{
  path: 'create',
  component: TimeEntryCreateComponent,
  data: {
    title: 'Time Entry',
    mainMenu: 'Time Entry',
    hideTitle: true,
    urls: []
  }
},
{
  path: 'create/:title',
  component: TimeEntryCreateComponent,
  data: {
    title: 'Time Management',
    mainMenu: 'Time Management',
    hideTitle: true,
    urls: []
  }
},
{
  path: 'edit/:id',
  component: TimeEntryCreateComponent,
  data: {
    title: 'Time Entry',
    mainMenu: 'Time Entry',
    hideTitle: true,
    urls: []
  }
},
{
  path: 'edit/:title/:id',
  component: TimeEntryCreateComponent,
  data: {
    title: 'Time Management',
    mainMenu: 'Time Management',
    hideTitle: true,
    urls: []
  }
},
{
  path: 'details/:id',
  component: TimeEntryDetailsComponent,
  data: {
    title: 'Time Entry',
    mainMenu: 'Time Entry',
    hideTitle: true,
    urls: []
  }
},

];

@NgModule ({
  imports: [RouterModule.forChild (routes)],
  exports: [RouterModule]
})
export class TaskEntryRoutingModule { }
