import { BadgeComponent } from './../../widget/badge/badge.component';
import { CustomRenderComponent } from './../../core/render/custom-render/custom-render.component';
import { TableMoreOptionComponent } from './../../core/render/tablemoreoption/tablemoreoption.component';
import { timeChart } from './../../core/mock/time-entry';
import { TimeEntryService } from './../../core/service/time-entry/time-entry.service';
import { SubSink } from 'subsink';
import { D3DonutGraphDataFromParent} from './../../core/interface/common.interface';
import { Router } from '@angular/router';
import { tableOption, tableConfig, ApiStatus, tableMoreOption, commonEvent, timeSheetFilter, dateFilter } from './../../core/constants/constants';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';

@Component ({
  selector: 'app-task-entry-landing-page',
  templateUrl: './task-entry-landing-page.component.html',
  styleUrls: ['./task-entry-landing-page.component.scss']
})
export class TaskEntryLandingPageComponent implements OnInit , OnDestroy {
  @ViewChild ('timeModal') timeModal: any;

  @ViewChild ('exportModal') exportModal: any;

  public TimeSheet ;

  public timeTypeCount = 0 ;

  public timeSheetFilter = timeSheetFilter;

  public dateFilter = dateFilter;

  public selectedDateFilter = null;

  public activeTimeSheetFilter = 0;

  public timeType:D3DonutGraphDataFromParent[];

  public tableLimitOption = tableConfig.tableLimitOption;

  public tableLimit = tableConfig.tableLimit;

  public tableSearch = '';

  public currentPage = 1;

  public gridData:[];

  public totalCount = 0;

  public itemsPerPage = tableConfig.pager;

  public tableOption = tableOption;

  public descriptionChart = [];

  public  stackedChartConfiguration = {
      width: 400,
      height: 320,
      margin: { top: 40, right: 40, bottom: 40, left: 40 },
      offsetHeight: 40
    }

  public tableMoreOption = tableMoreOption;

  public timeEntryDetails = {
    EndDate: '-',
    EndTime: '-',
    StartDate: '-',
    StartTime: '-',
    WBSElementName: '-',
    WBSShortDescription: '-',
    comment: '-',
    id: 0,
    companyCode: '-'
  }

  public tableSettings = {
    column: {
      '': {
        title: '',
        class: 'pointer',
        icon: '',
        type: 'custom',

        renderComponent: TableMoreOptionComponent,
        valuePrepareFunction: (value, row) => {
          if (row.id) {
            return this.tableMoreOption;
          } else {
            return '';
          }
        }
      },
      id: {
        title: 'Id',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      assignee: {
        title: 'Assignee',
        icon: '',
        class: 'pointer',
        type: 'custom',
        renderComponent: CustomRenderComponent,
        valuePrepareFunction: (value,row) => {
          return row?.assignee?.name;
        }
      },
      wbselementname: {
        title: 'WBS Element Name',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },

      wbsshortdescription: {
        title: 'WBS Short Description',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      start_date: {
        title: 'Start Date',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      start_time: {
        title: 'Start Time',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      end_time: {
        title: 'End Time',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      end_date: {
        title: 'End Date',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      approver: {
        title: 'Approver',
        icon: '',
        class: 'pointer',
        type: 'custom',
        renderComponent: CustomRenderComponent,
        valuePrepareFunction: (value,row) => {
          if ( row?.approval_level_set) {
          if ( row?.approval_level_set?.length !== 0) {
            return row?.approval_level_set[0]?.level1?.approver;
          }
        }
          return '-';
        }
      },
      ApprovalStatus: {
        title: 'Status',
        type: 'custom',
        renderComponent: BadgeComponent,
        valuePrepareFunction: (value, row) => {
          if (row) {
            if (row.status=== 'APPROVED') {
              value = {
                color: '#9DE10A',
                value: row.status,
                fontColor: 'white'
              };
            }  else  if (row.status === 'CANCELLED') {
              value = {
                color: '#ff5252',
                value: row.status,
                fontColor: 'white'
              };

            } else  if (row.status === 'REJECTED'){
              value = {
                color: '#e00404',
                value: row.status,
                fontColor: 'white'
              };
            }   else  if (row.status === 'PENDING'){
              value = {
                color: '#f77300',
                value: row.status,
                fontColor: 'white'
              };
            } else {
              value = {
                color: '',
                value: row.status,
                fontColor: '#000000'
              };
            }

          }
          return value;
        }
      },
  }
  }

  private subs = new SubSink ();

constructor( private _router: Router, private _timeService: TimeEntryService,) { }

  ngOnInit(): void {
    this.gridData = [];
    this.timeType = [];
this.setChart () ;
this.timeDetails ();
  }


  setChart() {
    this.subs.add (
      this._timeService.chart ( ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.descriptionChart  =  res?.timeentry?.time_hours_by_project?.data;
          this.timeType = res?.timeentry?.time_spend_by_type?.data;
          this.TimeSheet = timeChart.bar.data;
          this.timeTypeCount = res?.timeentry?.time_spend_by_type?.count;
        }
      })
    );
  }

  customEvent(event) {
    if ( event.event === commonEvent.view) {
      this.timeEntryDetails = {
        EndDate: event.data.end_date,
        EndTime: event.data.end_time,
        StartDate: event.data.start_date,
        StartTime: event.data.end_date,
        WBSElementName: event.data.wbselementname,
        WBSShortDescription: event.data.wbsshortdescription,
        comment: event.data.comment,
        id: event.data.id,
        companyCode: event.data.CompanyCode?.value
      };
      this.openModal ();
    }
    if ( event.event === commonEvent.edit) {
      this._router.navigate ([`/task-entry/edit/${event?.data?.id}`]);
    }
    if ( event.event === commonEvent.delete) {
}
  }

  openModal(){
    this.timeModal.show ();
  }

  closeModal() {
    this.timeModal.hide ();
  }
  // horizontalChart(res) {
  //   const color = [];
  //   const label = [];
  //   const data = [];
  //   res.forEach ((element, i) => {
  //     label.push (element.type);
  //     element.details.forEach ((el, j) => {
  //       if (!data[j]) {
  //         data[j] = { name: '', data: [] };
  //       }
  //       const label = data[j].name = el.label;
  //       data[j].data.push (el.value);
  //       color[j] = el.color;
  //     });
  //   });
  //   this.TimeSheet.data = data;
  //   this.TimeSheet['label'] = label;
  //   this.TimeSheet['color'] = color;
  // }

  create() {
    this._router.navigate (['/task-entry/create']);
  }

  onTimesheetFilterClick(event) {
    this.activeTimeSheetFilter = event.index;
  }

  dataFilterClick(event) {
    this.selectedDateFilter = event.index;
  }

tablePageChange(offSet: number) {
    this.currentPage = (offSet);
    this.timeDetails ( offSet , this.tableSearch, this.tableLimit);
  }

 timeDetails( offSet = 1, search = this.tableSearch, limit = this.tableLimit) {
    const gridParams = {
      q: search,
      page: offSet,
      limit: limit,
    };
    this.subs.add (
      this._timeService.grid ( gridParams).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.gridData = res['time-entry-list']?.result;
          this.totalCount =  res['time-entry-list']?.total_count;
        }
      })
    );
  }

  search() {
    this.currentPage = 1;
    this.timeDetails ();
  }

  downloadCsv() {
    window.open (this._timeService.timeEntryCsv (), '_self');
  }

downloadXls() {
    window.open (this._timeService.timeEntryXls (), '_self');
  }

  openExportPopUp() {
    this.exportModal.show ();
  }

closeExportModal() {
    this.exportModal.hide ();
  }

  ngOnDestroy() {
    this.subs.unsubscribe ();
  }
}
