import { SubSink } from 'subsink';
import { TimeManagementService } from './../../core/service/time-management/time-management.service';
import { ApiStatus } from 'src/app/core/constants/constants';
import { Router, ActivatedRoute } from '@angular/router';
import { Component, OnInit, OnDestroy } from '@angular/core';


@Component ({
  selector: 'app-time-entry-details',
  templateUrl: './time-entry-details.component.html',
  styleUrls: ['./time-entry-details.component.scss']
})
export class TimeEntryDetailsComponent implements OnInit , OnDestroy {

  private subs = new SubSink ();

  public timeDetails: TimeDetails;

  public comment:string;

  private id: string;

  public userList: string[];

  constructor(
     private _router: Router,
     private _timeService: TimeManagementService,
     private _activated: ActivatedRoute) { }

ngOnInit(): void {
  this.comment = '';
  this.subs.add (
    this._activated.params.subscribe (res => {
      this.id = res['id'];
      if (this.id) {
        this.getDetail ();
      }
    })
  );
  }

  view(){
      this._router.navigate (['/task-entry']);
  }


  getDetail () {
    this.subs.add (
      this._timeService.timeDetails (this.id ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          delete res.Status;
          this.timeDetails = res[0];
          if (this.timeDetails?.approval_level_set){
            this.timeDetails['approver'] = this.timeDetails?.approval_level_set[0]?.level1?.approver;
          }
      }
}
    ));
  }

  ngOnDestroy() {
    this.subs.unsubscribe ();
  }
}

interface TimeDetails {
  id: number;
  deleted_at?: any;
  created_date?: any;
  modified_date?: any;
  wbselementname: string;
  wbsshortdescription?: any;
  comment: string;
  start_date: string;
  end_date: string;
  status: string;
  start_time: string;
  end_time: string;
  created_by?: any;
  updated_by?: any;
  workflow: string;
  assignee: {id:string,name:string}
  CompanyCode: {value:  string;},
  approver:string;
  approval_level_set : approvalLevelSet[]
}
interface approvalLevelSet {
  level1: Level1;
}

interface Level1 {
  request_id: number;
  level: number;
  is_active: boolean;
  closed: boolean;
  request_type: string;
  approver: string;
}
