import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { environment } from 'src/environments/environment';
import { of } from 'rxjs';
import { timeChart } from './../../mock/time-entry';
import { ApiEndpoints } from './../../constants/constants';
import { HttpService } from './../common/http.service';
import { Injectable } from '@angular/core';

@Injectable ({
  providedIn: 'root'
})
export class TimeEntryService {


  constructor(private http: HttpService,
    private _function: FunctionService
    ) { }

grid(gridParams = { q: '', page: 0, limit: 10}) {
        const keys: any = Object.keys (gridParams);
        const value: any =  Object.values (gridParams);
          let query = '?';
          for (let i = 0; i < keys.length; i++) {
            if (value[i] !== '') {
              query += keys[i] + '=' + encodeURIComponent (value[i]) + '&';
            }
          }
       return this.http.get (ApiEndpoints.timeEntryGrid+ query);
      }

      gridDate(gridParams = { q: '', offset: 0, limit: 10 , start_date: this._function.convertToServerDate (new Date ()) , end_date: this._function.convertToServerDate (new Date ())}) {
        const keys: any = Object.keys (gridParams);
        const value: any =  Object.values (gridParams);
          let query = '?';
          for (let i = 0; i < keys.length; i++) {
            if (value[i] !== '') {
              query += keys[i] + '=' + encodeURIComponent (value[i]) + '&';
            }
          }
       return this.http.get (ApiEndpoints.timeEntryDateWise+ query);
      }

      chart(){
      //  return of (timeChart);
        return this.http.get (ApiEndpoints.timeEntryChart);
      }

      timeEntryXls() {
          return  environment.baseUrl+ environment.apiVersion +  ApiEndpoints.timeEntryXls;
      }

      timeEntryCsv() {
          return  environment.baseUrl+ environment.apiVersion + ApiEndpoints.timeEntryCsv;
      }

      projectList() {
        return this.http.get (ApiEndpoints.projectList);
    }

    userList() {
      return this.http.get (ApiEndpoints.userList);
  }

    timeDetails(id) {
      return this.http.get (ApiEndpoints.timeEntryDetails + '?id='+ id);
  }

    taskList() {
      return this.http.get (ApiEndpoints.taskList);
  }

  create(formData) {
    return this.http.post (ApiEndpoints.timeEntryCreate , formData);
}

edit(formData ,id) {
  return this.http.put (ApiEndpoints.timeEntryEdit + id + '/edit/' , formData);
}

    }

