import { CustomRenderComponent } from './../../core/render/custom-render/custom-render.component';
import { FunctionService } from 'src/app/core/helper/function.helper.service';
import { TimeEntryService } from './../../core/service/time-entry/time-entry.service';
import { tableOption, ApiStatus, tableConfig } from './../../core/constants/constants';
import { SubSink } from 'subsink';
import { toastMessages } from './../../core/constants/warning-messages';
import { Router, ActivatedRoute } from '@angular/router';
import { ToastService } from './../../core/helper/toast.helper.service';
import { FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit, OnDestroy } from '@angular/core';
import { ValidationruleService } from 'src/app/core/helper/validationrule.service';

@Component ({
  selector: 'app-time-entry-create',
  templateUrl: './time-entry-create.component.html',
  styleUrls: ['./time-entry-create.component.scss']
})
export class TimeEntryCreateComponent implements OnInit , OnDestroy {
  public tableOption = tableOption

  public timeEntryForm!: FormGroup;

  public tableLimitOption = tableConfig.tableLimitOption;

  public tableLimit = tableConfig.tableLimit;

  public tableSearch = '';

  public currentPage = 1;

  public gridData:[];

  public totalCount = 0;

  public itemsPerPage = tableConfig.pager;

  public timeDetails: TimeDetails;

  public submitted = false;

  public userLists = false;

  public userRole = this._helper.getUser?.user_type;

  public today = new Date ();

  public dropdownConfigWBSElementName = {
    displayKey: 'WBSElementName',
    search: true,
    clearOnSelection: true,
    placeholder: 'select',
    height: '150px'
  };

  public dropdownConfigName = {
    displayKey: 'name',
    search: true,
    clearOnSelection: true,
    placeholder: 'select',
    height: '150px'
  };

  public   dropdownConfig = {
    search: true,
    clearOnSelection: true,
    placeholder: 'select',
    height: '150px'
  };

  public tableSettings = {
    column: {
      id: {
        title: 'Id',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      assignee: {
        title: 'Assignee',
        icon: '',
        class: 'pointer',
        type: 'custom',
        renderComponent: CustomRenderComponent,
        valuePrepareFunction: (value,row) => {
          return row?.assignee?.name;
        }
      },
      wbselementname: {
        title: 'WBS Element Name',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },

      wbsshortdescription: {
        title: 'WBS Short Description',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      start_date: {
        title: 'Start Date',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      start_time: {
        title: 'Start Time',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      end_time: {
        title: 'End Time',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      end_date: {
        title: 'End Date',
        icon: '',
        clickable: true,
        class: 'pointer',
        type: 'text',
      },
      approver: {
        title: 'Approver',
        icon: '',
        class: 'pointer',
        type: 'custom',
        renderComponent: CustomRenderComponent,
        valuePrepareFunction: (value,row) => {
          return row?.approval_level_set[0]?.level1?.approver;
        }
      }
  }
  }


public dropdownConfigWbsShortDescription = {
    displayKey: 'wbsshortdescription',
    search: true,
    clearOnSelection: true,
    placeholder: 'select',
    height: '150px'
  };

public project = [ ];

    public wbsShortDescription = [];

   public type = [
      'working',
      'Leave',
      ]

      private timeMange = false;

      private subs = new SubSink ();

      private id:string;

  constructor( private _router: Router,
    private _formBuilder: FormBuilder,
    private _rule: ValidationruleService,
    private _helper : FunctionService,
    private _toast: ToastService,
    private activated: ActivatedRoute,
    private _timeService: TimeEntryService) { }

ngOnInit(): void {

  this.timeGrid ();
  if (this.userRole === 'Admin') {
      this.userList ();
  }
    this.subs.add (
      this.activated.params.subscribe (res => {
          if (res['title']) {
            this.timeMange = true;
          }
        this.id = res['id'];
        if (this.id) {
          this.setForm (true);
          this.getDetail ();
        } else {
          this.timeGrid ();
          this.setForm (true);
        }
      })
    );

    this.dropDownList ();
  }

  get formControl() {
    return this.timeEntryForm.controls;
  }

  dropDownList() {
    this.subs.add (
      this._timeService.projectList ( ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          delete res.Status;
          this.project = [];
          res.map ((item)=>  this.project.push ({WBSElementName: item?.WBSElementName , WBSCompanyCode: item?.WBSCompanyCode , WBSShortdescription: item?.WBSShortdescription}));
      }
}
    ));
//     this.subs.add (
//       this._timeService.taskList ( ).subscribe (res => {
//         if (ApiStatus.ok === res.status) {
//           delete res.Status;
//           this.wbsShortDescription = res;
//       }
// }
//     ));
  }

  userList () {
    this.subs.add (
      this._timeService.userList ( ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          delete res.Status;
          this.userLists = res;
   }
}));
  }

  timeGrid( offSet = 1, search = this.tableSearch, limit = this.tableLimit ,  startDate = this._helper.convertToServerDate (new Date ()) ,endDate = this._helper.convertToServerDate (new Date ()) ) {
    const gridParams = {
      q: search,
      offset: (offSet - 1) * this.tableLimit,
      limit: limit,
      start_date: startDate ,
      end_date: endDate
    };
    this.subs.add (
      this._timeService.gridDate ( gridParams).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this.gridData = res?.results;
          this.totalCount =  res?.count;
        }
      })
    );
  }

  tablePageChange(offSet: number) {
    this.currentPage = (offSet);
    this.timeGrid ( offSet , this.tableSearch, this.tableLimit);
  }

  dateChange () {
  const startDate =  this._helper.convertToServerDate ( this.timeEntryForm.value.startDate );
  const endDate =  this._helper.convertToServerDate ( this.timeEntryForm.value.endDate );
    this.timeGrid (  1,  this.tableSearch, this.tableLimit ,startDate , endDate );
  }

  search() {
    this.currentPage = 1;
    this.timeGrid ();
  }

  getDetail () {
    this.subs.add (
      this._timeService.timeDetails (this.id ).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          delete res.Status;
          this.timeDetails = res[0];
          this.timeGrid (  1,  this.tableSearch, this.tableLimit ,this.timeDetails?.start_date ,this.timeDetails?.end_date );
          this.setForm (false);
      }
}
    ));
  }

  setForm(create: boolean) {
    if (create) {
      this.timeEntryForm = this._formBuilder.group ({
        startDate: [new Date () ,this._rule.requiredValidation],
        endDate: [new Date () ,this._rule.requiredValidation],
        wbselementname: ['', [this._rule.requiredValidation]],
        wbsshortdescription: [''],
      //  type: ['', [this._rule.requiredValidation]],
        comment: ['',  this._rule.textBoxValidation ()],
        startTime: ['', [this._rule.timeValidator,this._rule.requiredValidation]],
        endTime: ['', [this._rule.timeValidator,this._rule.requiredValidation]],
        assignee: []
      });
    } else {
      this.timeEntryForm = this._formBuilder.group ({
        startDate: [this.timeDetails?.start_date ? new Date (this.timeDetails?.start_date) :new Date () ,this._rule.requiredValidation],
        endDate: [this.timeDetails?.end_date ? new Date ( this.timeDetails?.end_date) : new Date () ,this._rule.requiredValidation],
        wbselementname: [{WBSElementName: this.timeDetails.wbselementname , WBSCompanyCode: this.timeDetails?.CompanyCode?.value , WBSShortdescription: this.timeDetails.wbsshortdescription}, [this._rule.requiredValidation]],
        wbsshortdescription: [this.timeDetails.wbsshortdescription],
       // type: ['working', [this._rule.requiredValidation]],
        comment: [this.timeDetails.comment,  this._rule.textBoxValidation ()],
        startTime: [this.timeDetails.start_time.slice (0, -3), [this._rule.timeValidator,this._rule.requiredValidation]],
        endTime: [this.timeDetails.end_time.slice (0, -3), [this._rule.timeValidator,this._rule.requiredValidation]],
        assignee: []
      });
    }
  }

  submit(){
    this.submitted = true;
    const formValue =  this.timeEntryForm.value;

    if (this.timeEntryForm.invalid) {
      this._toast.error (toastMessages.validation);
      return;
    }
    if ( !this.timeValidation (formValue.startTime,formValue.endTime,this.timeEntryForm.value.startDate,this.timeEntryForm.value.endDate)) {
      this._toast.error (toastMessages.Date);
      return;
    }
    const formData = {
      wbselementname: formValue.wbselementname?.WBSElementName,
      wbsshortdescription: formValue.wbselementname?.WBSShortdescription,
      comment: formValue.comment,
      start_date: this._helper.convertToServerDate ( formValue.startDate),
      end_date: this._helper.convertToServerDate ( formValue.endDate ),
      start_time: this._helper.timeConverter (formValue.startTime),
      assignee: formValue.assignee?.pk,
      end_time: this._helper.timeConverter (formValue.endTime)};
    if (this.id) {
      this.edit (formData);
    } else {
      this.create (formData);
   }

  }

  timeValidation (st,et,sd,ed) {
  const  startDate =  sd.setHours (0,0,0,0);
  const  endDate = ed.setHours (0,0,0,0);
  const  startTime  = + this._helper.timeConverter (st).split (':',1);
  const  endTime = + this._helper.timeConverter (et).split (':',1);
  const  startMin  = + this._helper.timeConverter (st).split (':',2)[1];
  const  endMin =  + this._helper.timeConverter (et).split (':',2)[1];
  if (startDate === endDate) {
        if ( startTime > endTime){
          return false;
        } else if ( startTime === endTime){
          if (!isNaN (startMin) && !isNaN (endMin) ) {
            if (startMin >= endMin ) {
              return false;
            } else {
              return true;
            }
          } else {
            return false;
          }
        } else {
          return true;
        }

  } else {
    return true;
  }
  }

  create(formData ) {
    this.subs.add (
      this._timeService.create (formData).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this._toast.success (res.message);
          this.view ();
      }
}
    ));

  }

  edit(formData ) {
    this.subs.add (
      this._timeService.edit (formData,this.id).subscribe (res => {
        if (ApiStatus.ok === res.status) {
          this._toast.success (res.message);
          this.view ();
      }
}
    ));

  }

view(){
  if ( this.timeMange) {
    this._router.navigate (['/time-management']);
  } else {
    this._router.navigate (['/task-entry']);
  }
}

ngOnDestroy(): void {
  this.subs.unsubscribe ();
}
}

interface TimeDetails {
  id: number;
  deleted_at?: any;
  created_date?: any;
  modified_date?: any;
  wbselementname: string;
  wbsshortdescription?: any;
  comment: string;
  start_date: string;
  end_date: string;
  start_time: string;
  end_time: string;
  created_by?: any;
  updated_by?: any;
  CompanyCode: {value:  string;}
}
